export default class LRUCacheMap extends Map {
    _capacity;
    constructor(capacity = 500) {
        super();
        this._capacity = capacity;
    }
    get(key) {
        let value = super.get(key);
        if (value !== undefined) {
            super.delete(key);
            super.set(key, value);
        }
        return value;
    }
    set(key, value) {
        if (super.has(key)) {
            super.delete(key);
        }
        else if (super.size === this._capacity) {
            super.delete(super.keys().next().value);
        }
        super.set(key, value);
        return this;
    }
}
