export function mode(iter) {
    let maxCount = 0;
    let maxValue;
    let map = new Map();
    for (let value of iter) {
        let count = map.get(value) ?? 0;
        map.set(value, ++count);
        if (count > maxCount) {
            maxCount = count;
            maxValue = value;
        }
    }
    return maxValue;
}
